import Slider from 'react-slick'
import styled from 'styled-components'
import Slide from './Slide'

import ArrowBackIosNewIcon from '@mui/icons-material/ArrowBackIosNew'
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos'

import 'slick-carousel/slick/slick.css'
import 'slick-carousel/slick/slick-theme.css'
import { IconButton } from '@mui/material'
import CustomArrow from '../../../CustomArrow'

function HeaderSlider() {
  const settings = {
    nextArrow: (
      <CustomArrow
        name="next"
        icon={
          <IconButton>
            <ArrowForwardIosIcon />
          </IconButton>
        }
      />
    ),
    prevArrow: (
      <CustomArrow
        name="prev"
        icon={
          <IconButton>
            <ArrowBackIosNewIcon />
          </IconButton>
        }
      />
    ),
    dots: true,
    fade: true,
    infinite: true,
    autoplay: true,
    autoplaySpeed: 5000,
    speed: 1500,
    slidesToShow: 1,
    slidesToScroll: 1,
  }

  const slideInfo = [
    {
      name: '대구 Cafe MINICLIP',
      addr: '대구광역시 북구 호암로 15',
      image: `${process.env.PUBLIC_URL}/images/building_2.jpg`,
    },
    {
      name: '청주 리버사이드 2차',
      addr: '청주시 흥덕구 대율로 341',
      image: `${process.env.PUBLIC_URL}/images/building_5.jpg`,
    },
    {
      name: '서울 국립현대미술관',
      addr: '서울특별시 중구 삼청로 30',
      image: `${process.env.PUBLIC_URL}/images/building_6.jpg`,
    },
  ]

  return (
    <StyledSlider {...settings}>
      {slideInfo.map((slide, index) => (
        <Slide
          key={index}
          name={slide.name}
          addr={slide.addr}
          imgPath={slide.image}
        />
      ))}
    </StyledSlider>
  )
}
export default HeaderSlider

export const StyledSlider = styled(Slider)`
  poisiton: relative;

  .prev,
  .next {
    position: absolute;
    top: 50%;
    transform: translate(0, -50%);
    z-index: 3;
    display: none;
  }

  .prev svg,
  .next svg {
    font-size: 36px;
    color: white;
  }

  .prev {
    @media screen and (min-width: 500px) {
      left: 80px;
    }

    @media screen and (max-width: 500px) {
      left: 50px;
    }
  }

  .next {
    @media screen and (min-width: 500px) {
      right: 80px;
    }

    @media screen and (max-width: 500px) {
      right: 50px;
    }
  }

  .slick-track {
    height: 100vh;
  }

  .slick-dots {
    @media screen and (min-width: 500px) {
      bottom: 50px;
      right: 80px;
    }

    @media screen and (max-width: 500px) {
      bottom: 50px;
      left: 50%;
      transform: translate(-50%, 0);
    }

    width: auto;
  }

  .slick-dots li {
    margin: 0 7px;
  }

  .slick-dots li.slick-active button:before {
    opacity: 1;
    background: white;
    color: transparent;
  }

  .slick-dots li button:before {
    width: 18px;
    height: 18px;
    font-size: 0px;
    line-height: 0px;
    opacity: 1;
    color: transparent;
    border-radius: 100%;
    border: 1px solid white;
  }
`
