import styled from 'styled-components'
import ResponsiveText from '../../../../ResponsiveText'

import { useInView } from 'react-intersection-observer'

function Welcome() {
  const [ref, inView] = useInView({
    /* Optional options */
    threshold: 0.3,
    triggerOnce: true,
  })

  return (
    <Container ref={ref}>
      <TextContainer style={inView ? { opacity: '1' } : { opacity: '0' }}>
        <Logo className={inView ? 'fade-in-bottom-1-2' : null}>청담,</Logo>
        <Description
          className={inView ? 'fade-in-bottom-1-2' : null}
          style={inView ? { animationDelay: '0.2s' } : null}
        >
          <ResponsiveText>
            사람과 더불어 늘 깨어있는 정신으로 새로운 가치를 창출합니다.{' '}
          </ResponsiveText>
          <ResponsiveText>
            여기는 소개 멘트입니다. 사장님께서 원하시는 문구 적어주시면 됩니다.{' '}
          </ResponsiveText>

          <ResponsiveText>
            쓸게없네요. 소개멘트 마치겠습니다. 감사합니다.
          </ResponsiveText>
        </Description>
      </TextContainer>
      <ImageContainer>
        <img src="images/sketch.png" alt="sketch" />
      </ImageContainer>
    </Container>
  )
}

const Container = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  height: 30%;
  position: relative;
  font-family: chosunSm;
  @media (max-width: 650px), (max-height: 800px) {
    height: auto;
    padding: 40px;
  }
`

const ImageContainer = styled.div`
  position: absolute;
  bottom: 0px;
  right: 0;
  z-index: -1;

  @media screen and (max-width: 650px) {
    left: 50%;
    transform: translate(-50%, 0%);
  }

  img {
    height: 100%;
    object-fit: cover;
    opacity: 0.15;
  }
`

const TextContainer = styled.div`
  display: flex;
  justify-content: space-between;
  gap: 8rem;

  @media screen and (max-width: 950px) {
    flex-direction: column;
    gap: 0;
  }

  align-items: center;
`

const Logo = styled.div`
  font-size: max(4rem, 76px);

  color: var(--primary);
  white-space: nowrap;

  @media screen and (max-width: 950px) {
    font-size: 56px;
    margin-bottom: 20px;
  }
  @media screen and (max-width: 450px) {
    font-size: 42px;
  }
`

const Description = styled.div`
  text-align: center;

  font-size: max(1.2rem, 23px);
  line-height: max(2.1rem, 41px);

  @media screen and {
    white-space: nowrap;
  }

  @media screen and (max-width: 950px) {
    font-size: 21px;
    line-height: 36px;
  }
  @media screen and (max-width: 650px) {
    margin-left: 0;
    width: 90vw;
  }
  @media screen and (max-width: 450px) {
    font-size: 18px;
  }
`

export default Welcome
