import styled from 'styled-components'
import React from 'react'
import { Scrollbar } from 'smooth-scrollbar-react'
import { useParams } from 'react-router-dom'

import Navbar from '../components/Navbar/Navbar'
import FooterBar from '../components/FooterBar/FooterBar'
import { BoardData } from '../components/Board/BoardData'

import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp'
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown'
import StyledLink from '../components/StyledLink'

function BoardDetail() {
  const [enableScroll, setScroll] = React.useState(true)
  const { id } = useParams()

  const boardData = new BoardData()

  const post = boardData.getPost(id)
  const prevPost = boardData.getPrevPost(id)
  const nextPost = boardData.getNextPost(id)

  const typeKorDict = {
    all: '전체',
    guide: '안내',
    bid: '입찰',
    award: '수상',
    recruitment: '채용',
    news: '뉴스',
  }

  return (
    <Scrollbar
      plugins={{
        overscroll: {
          effect: 'glow',
        },
      }}
    >
      <Scroll>
        <Container>
          <Navbar fixMenuIdx={1} fixSubmenuIdx={2} setScroll={setScroll} />
          <ContentContainer style={enableScroll ? null : { display: 'none' }}>
            <Content>
              <PostInfo>
                <Title>{post.title}</Title>
                <SubInfo>
                  <div>{post.created}</div>
                  <div>{typeKorDict[post.type]}</div>
                </SubInfo>
              </PostInfo>
              <PostContent>
                {`안녕하세요. 게시글 내용입니다. 지금은 더미 기능으로, 나중에 기능을 연결하면 등록한 게시글 내용이 나오게 됩니다.\n
                안녕하세요. 게시글 내용입니다. 지금은 더미 기능으로, 나중에 기능을 연결하면 등록한 게시글 내용이 나오게 됩니다.`}
              </PostContent>
              <PrevNextPost>
                {prevPost !== undefined ? (
                  <StyledLink to={`/about/board/${parseInt(id) - 1}`}>
                    <div>
                      <KeyboardArrowUpIcon /> 이전글
                    </div>
                    <div className="title">{prevPost.title}</div>
                  </StyledLink>
                ) : (
                  <div>
                    <KeyboardArrowUpIcon /> 이전글
                  </div>
                )}
              </PrevNextPost>
              <PrevNextPost>
                {nextPost !== undefined ? (
                  <StyledLink to={`/about/board/${parseInt(id) + 1}`}>
                    <div>
                      <KeyboardArrowDownIcon /> 다음글
                    </div>
                    <div className="title">{nextPost.title}</div>
                  </StyledLink>
                ) : (
                  <div>
                    <KeyboardArrowDownIcon /> 다음글
                  </div>
                )}
              </PrevNextPost>
            </Content>

            <StyledLink to="/about/board">
              <ExitButton>목&nbsp;&nbsp;&nbsp;&nbsp;록</ExitButton>
            </StyledLink>
          </ContentContainer>
          <FooterBar style={enableScroll ? null : { display: 'none' }} />
        </Container>
      </Scroll>
    </Scrollbar>
  )
}

const Scroll = styled.div`
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
  position: fixed;
  height: 100vh;
  ::-webkit-scrollbar {
    display: none; /* Chrome, Safari, Opera*/
  }
`

const Container = styled.div`
  height: auto;
  min-height: 700px;
`

const ContentContainer = styled.div`
  position: relative;
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: max(4rem, 40px);
  padding: max(6rem, 40px) 12rem;
  font-family: score;

  align-items: center;

  @media (max-width: 920px) {
    padding: max(4rem, 40px) 6rem;
  }
`

const Content = styled.div`
  width: 100%;
  border-top: 3px solid var(--primary);
  border-bottom: 3px solid var(--primary);
`

const PostInfo = styled.div`
  display: flex;
  flex-direction: column;

  align-items: flex-start;
  justify-content: space-between;
  border-bottom: 2px solid var(--light-300);
  gap: 1rem;
  padding: max(1.5rem, 24px) max(2rem, 36px);
`

const Title = styled.div`
  color: black;
  font-size: max(1.2rem, 19px);
  font-weight: 500;
  text-align: center;
`

const SubInfo = styled.div`
  display: flex;
  justify-content: flex-end;
  font-size: max(0.8rem, 14px);
  gap: 1rem;
`

const PostContent = styled.div`
  font-size: max(1rem, 16px);
  line-height: max(1.5rem, 27px);
  white-space: pre-line;
  padding: max(3rem, 24px) max(2rem, 36px) !important;
`

const PrevNextPost = styled.div`
  border-top: 2px solid var(--light-300);
  * {
    font-size: max(0.9rem, 16px) !important;
  }

  div {
    display: flex;
    align-items: center;
  }

  a {
    display: flex;
    gap: max(2rem, 20px);
    color: black;
  }

  a > .title {
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  padding: max(1rem, 14px) max(2rem, 36px);
  white-space: nowrap;
`

const ExitButton = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;

  width: max(11rem, 160px);
  height: max(3rem, 50px);

  background: var(--primary);
  color: white;

  font-size: max(0.8rem, 16px);
`

export default BoardDetail
