function InfoLabel({ children }) {
  return <LabelContainer>{children}</LabelContainer>
}

const LabelContainer = styled.div`
  font-family: score;
  font-weight: 600;
  font-size: max(1.6rem, 24px);

  padding: max(0.9rem, 8px);
  border-bottom: 3px solid var(--primary);

  color: var(--primary);
`

function InfoIconWidget({ image, label, value }) {
  return (
    <IconWidgetContainer>
      <img src={image} alt={image} />
      <span className="label">{label}</span>
      <span className="value">{value}</span>
    </IconWidgetContainer>
  )
}

const IconWidgetContainer = styled.div`
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: max(1rem, 16px);

  font-family: score;

  .label {
    font-weight: 500;
    font-size: max(1rem, 16px);
  }

  .value {
    font-size: max(1.2rem, 18px);
  }

  img {
    width: max(4rem, 64px);
    object-fit: cover;
  }
`

function InfoWidget({ label, value }) {
  return (
    <InfoWidgetContainer>
      <span className="label">{label}</span>
      <span>{value}</span>
    </InfoWidgetContainer>
  )
}

const InfoWidgetContainer = styled.div`
  display: flex;
  justify-content: space-between;
  font-family: score;
  font-size: max(1.1rem, 18px);
  border-bottom: 2px solid var(--light-600);
  padding: 0 max(2.2rem, 28px);
  padding-bottom: max(1.2rem, 18px);

  .label {
    font-weight: 500;
  }
`

function toArea(value) {
  return `${parseInt(value).toLocaleString()}m${'\xB2'}`
}

function WorkDetail() {
  const [enableScroll, setScroll] = React.useState(true)
  const { id } = useParams()

  const data = workData[parseInt(id)]

  const REGION_KOR_DICT = {
    Seoul: '서울특별시',
    Incheon: '인천광역시',
    Daejeon: '대전광역시',
    Daegu: '대구광역시',
    Gwangju: '광주광역시',
    Ulsan: '울산광역시',
    Busan: '부산광역시',
    'Gyeonggi-do': '경기도',
    'Gangwon-do': '강원도',
    'Chungcheong-do': '충청도',
    'Gyeongsang-do': '경상도',
    'Jeolla-do': '전라도',
    'Jeju-do': '제주도',
    others: '기타',
  }

  return (
    <ScrollBody>
      <Container>
        <Navbar fixMenuIdx={2} fixSubmenuIdx={0} setScroll={setScroll} />
        <ContentContainer style={enableScroll ? null : { display: 'none' }}>
          <Title>
            <div className="primary">{data.name}</div>
            {data.addr}
          </Title>

          <WorkImageSlider data={data.images} />

          <InfoFlexContainer>
            <Info>
              <InfoLabel>OVERVIEW</InfoLabel>
              <InfoContent
                gap="max(3rem, 40px)"
                padding="max(2.5rem, 40px) max(2.5rem, 20px)"
              >
                <InfoIconContainer>
                  <InfoIconWidget
                    image={`${process.env.PUBLIC_URL}/images/works/schedule.png`}
                    label="준공년도"
                    value={`${data.construction_date.substring(0, 4)}년`}
                  />
                  <InfoIconWidget
                    image={`${process.env.PUBLIC_URL}/images/works/map-location.png`}
                    label="시공지역"
                    value={`${REGION_KOR_DICT[data.region]}`}
                  />
                  <InfoIconWidget
                    image={`${process.env.PUBLIC_URL}/images/works/area.png`}
                    label="연면적"
                    value={toArea(data.total_floor_area)}
                  />
                  <InfoIconWidget
                    image={`${process.env.PUBLIC_URL}/images/works/floor.png`}
                    label="규모"
                    value={data.number_of_stories}
                  />
                </InfoIconContainer>
                <div className="description">{data.description}</div>
              </InfoContent>
            </Info>
            <Info>
              <InfoLabel>DETAILED INFO.</InfoLabel>
              <InfoContent
                gap={'max(1.6rem, 24px)'}
                padding="max(2.5rem, 40px) 0"
              >
                <InfoWidget
                  label="설계/준공년도"
                  value={`${data.design_date.substring(
                    0,
                    4
                  )}/${data.construction_date.substring(0, 4)}년`}
                />
                <InfoWidget
                  label="시공지역"
                  value={`${REGION_KOR_DICT[data.region]}`}
                />
                <InfoWidget
                  label="대지면적"
                  value={`${toArea(data.site_area)}`}
                />
                <InfoWidget
                  label="연면적"
                  value={`${toArea(data.total_floor_area)}`}
                />
                <InfoWidget
                  label="건축면적"
                  value={`${toArea(data.building_area)}`}
                />
                <InfoWidget label="규모" value={`${data.number_of_stories}`} />
              </InfoContent>
            </Info>
          </InfoFlexContainer>
          <StyledLink to="/works">
            <ExitButton>목&nbsp;&nbsp;&nbsp;&nbsp;록</ExitButton>
          </StyledLink>
        </ContentContainer>
        <FooterBar style={enableScroll ? null : { display: 'none' }} />
      </Container>
    </ScrollBody>
  )
}

const Container = styled.div`
  display: flex;
  flex-direction: column;
  height: auto;
  min-height: 100vh;
`

const ContentContainer = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: max(3rem, 40px);
  padding: max(4rem, 40px) 12rem;

  @media (max-width: 1800px) {
    padding: max(4rem, 40px) 6rem;
  }
`

const Title = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: max(0.8rem, 14px);

  font-family: score;
  font-weight: 300;
  font-size: max(1rem, 16px);
  color: black;

  .primary {
    font-weight: 500;
    font-size: max(2rem, 32px);
    color: var(--primary);
  }
`

const InfoFlexContainer = styled.div`
  display: grid;
  grid-template-columns: 6fr 4fr;
  gap: max(8rem, 30px);

  @media (max-width: 1050px) {
    grid-template-columns: 1fr;
  }

  .description {
    font-family: score;
    font-size: max(1.2rem, 18px);
    line-height: max(2.4rem, 36px);
  }
`

const Info = styled.div`
  display: flex;
  flex-direction: column;
`

const InfoContent = styled.div`
  display: flex;
  flex-direction: column;
  gap: ${props => props.gap};
  padding: ${props => props.padding};
`

const InfoIconContainer = styled.div`
  display: flex;
  justify-content: space-between;
`

const ExitButton = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;

  width: max(15rem, 180px);
  height: max(4rem, 60px);

  background: var(--primary);
  color: white;

  font-family: score;
  font-size: max(1.3rem, 21px);

  margin: 0 auto;
`
export default WorkDetail
