import styled from 'styled-components'
import FooterBar from '../FooterBar/FooterBar'
import Navbar from '../Navbar/Navbar'

function Body({ enableScroll, setScroll }) {
  return (
    <Container>
      <FlexContainer>
        <Navbar fixMenuIdx={1} fixSubmenuIdx={1} setScroll={setScroll} />
        <ContentContainer style={enableScroll ? null : { display: 'none' }}>
          <Image />
          <Content>
            <TextContainer>
              <Title className="fade-in-bottom">
                청담을 찾아주셔서 감사합니다.
              </Title>
              <Description
                className="fade-in-bottom"
                style={{ animationDelay: '0.5s' }}
              >
                <span>
                  CEO 인사말입니다. 사장님께서 하고싶은말 적어주세요.{' '}
                </span>
                <span>
                  청담 건축사사무소는 고객과의 약속을 최우선으로 생각합니다.{' '}
                  <br />
                </span>
                <br />
                <span>
                  힘찬 발걸음을 내딛고 있으며, 건축전문 조직으로서의 사명감을
                  위해{' '}
                </span>
                <span>
                  이익을 추구하지 않는 건축과 도시의 담론의 장을 마련하여{' '}
                </span>
                <span>
                  건축을 통한 사회공헌을 이루고 있습니다.
                  <br />
                </span>
                <br />

                <span>
                  보다 나은 미래를 창조하기 위해 최고의 디자인과 기술을 바탕으로{' '}
                </span>
                <span>고객을 만족시키는 든든한 파트너가 되겠습니다.</span>
              </Description>
              <Sign className="fade-in-bottom" style={{ animationDelay: '1s' }}>
                <div className="position">
                  <span>청담건축사사무소</span>
                  <span>
                    소장 <span className="name">주홍민</span>
                  </span>
                </div>
                <div className="sign" />
              </Sign>
            </TextContainer>
            <Person />
          </Content>
        </ContentContainer>
      </FlexContainer>
      <FooterBar style={enableScroll ? null : { display: 'none' }} />
    </Container>
  )
}

const Container = styled.div`
  width: 100vw;
`

const FlexContainer = styled.div`
  display: flex;
  flex-direction: column;
  height: 100%;
  height: 100vh;
  @media (max-height: 800px) {
    height: auto;
  }
`

const ContentContainer = styled.div`
  position: relative;
  width: 100%;

  @media (min-height: 800px) {
    flex: 1;
  }
  @media (max-height: 800px) {
    padding: 40px 0;
  }
`

const Image = styled.div`
  position: absolute;

  width: 100%;
  height: 100%;
  background-image: url('${process.env.PUBLIC_URL}/images/dotted_pattern.png');
  background-repeat: no-repeat;
  background-position: 50rem center;
  opacity: 0.7;

  @media (max-height: 800px) {
    display: none;
  }
`

const Content = styled.div`
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  height: 100%;
  margin: 0 15rem;

  @media (max-width: 1300px) {
    margin: 0 5rem;
  }
`

const TextContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: max(5rem, 40px);
  height: 100%;

  font-size: max(1rem, 18px);
  line-height: max(1.8rem, 28px);

  @media (max-width: 600px) {
    font-size: 16px;
    line-height: 24px;
  }
`

const Title = styled.div`
  font-family: chosunSm;
  font-size: max(2.5rem, 42px);
  line-height: 48px;
  color: var(--primary);

  word-break: keep-all;

  @media (max-width: 600px) {
    font-size: 36px;
  }
`

const Description = styled.div`
  font-family: score;

  color: black;

  span {
    display: block;
  }

  @media (max-width: 600px) {
    span {
      display: inline;
    }
  }
`

const Sign = styled.div`
  display: flex;
  position: relative;
  align-items: center;
  gap: 1.2rem;

  font-family: score;
  color: var(--primary);

  .position {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }

  .position .name {
    font-weight: 600;
  }

  .sign {
    flex: 1;
    height: 130%;
    min-height: 60px;
    background-image: url('${process.env.PUBLIC_URL}/images/sign.png');
    background-size: contain;
    background-repeat: no-repeat;
  }
`

const Person = styled.div`
  flex: 1;
  width: auto;
  height: 85%;
  background-image: url('${process.env.PUBLIC_URL}/images/ceo.png');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: right bottom;

  @media (max-width: 920px) {
    display: none;
  }
`

export default Body
