import styled from 'styled-components'
import React from 'react'

import Navbar from '../components/Navbar/Navbar'
import FooterBar from '../components/FooterBar/FooterBar'
import ScrollBody from '../components/ScrollBody'
import Box from '@mui/material/Box'
import TextField from '@mui/material/TextField'
import Button from '@mui/material/Button'
import DriveFolderUploadIcon from '@mui/icons-material/DriveFolderUpload'

import List from '@mui/material/List'
import ListItem from '@mui/material/ListItem'
import ListItemIcon from '@mui/material/ListItemIcon'
import ListItemText from '@mui/material/ListItemText'
import FolderIcon from '@mui/icons-material/Folder'
import DeleteIcon from '@mui/icons-material/Delete'
import IconButton from '@mui/material/IconButton'

import FormControlLabel from '@mui/material/FormControlLabel'
import Checkbox from '@mui/material/Checkbox'
import { useMediaQuery } from 'react-responsive'

function UploadButton({ useRef, onChange }) {
  return (
    <div>
      <Button
        disableElevation={true}
        variant="contained"
        component="label"
        endIcon={
          <DriveFolderUploadIcon
            sx={{ fontSize: 'max(1.3rem, 21px) !important' }}
          />
        }
        sx={{
          fontFamily: 'score',
          fontSize: 'max(1.1rem, 18px)',
        }}
      >
        파일 업로드
        <input ref={useRef} hidden multiple type="file" onChange={onChange} />
      </Button>
    </div>
  )
}

function UploadFileList({ isMoblie }) {
  return (
    <List>
      <ListItem
        disablePadding
        secondaryAction={
          <IconButton edge="end" aria-label="delete">
            <DeleteIcon
              sx={{
                fontSize: 'max(2rem, 32px)',
                color: 'var(--primary)',
              }}
            />
          </IconButton>
        }
        sx={{
          borderBottom: '2px solid var(--light-600)',
          paddingBottom: 'max(1rem, 18px)',
        }}
      >
        {isMoblie ? null : (
          <ListItemIcon sx={{ padding: 'max(0.5rem, 10px)' }}>
            <FolderIcon sx={{ fontSize: 'max(2rem, 32px)' }} />
          </ListItemIcon>
        )}
        <ListItemText
          sx={{
            span: {
              fontFamily: 'score !important',
              fontSize: 'max(1.2rem, 18px)',
            },
            '.MuiListItemText-secondary': {
              fontSize: 'max(1rem, 16px) !important',
            },
          }}
          primary="서울 센트럴자이 건축 견적서.hwp"
          secondary="244,012KB"
        />
      </ListItem>
    </List>
  )
}

function Inquiry() {
  const [enableScroll, setScroll] = React.useState(true)
  const [checked, setChecked] = React.useState(false)
  const fileRef = React.useRef()

  const handleFileChange = event => {
    for (let i = 0; i < fileRef.current.files.length; i++) {}
  }

  const isMoblie = useMediaQuery({ query: '(max-width: 550px)' })

  const handleCheck = event => {
    setChecked(event.target.checked)
  }

  return (
    <ScrollBody>
      <Container>
        <Navbar fixMenuIdx={3} fixSubmenuIdx={1} setScroll={setScroll} />
        <ContentContainer style={enableScroll ? null : { display: 'none' }}>
          <Title>문의할 내용을 입력해 주세요.</Title>

          <StyledBox component="form" noValidate autoComplete="off">
            <InputContainer>
              <TextFlexRow>
                <div
                  style={{
                    display: 'grid',
                    gridTemplateColumns: '1fr 1fr',
                    gap: '2rem',
                  }}
                >
                  <StyledTextField
                    id="first-name"
                    label="이름 (First Name)"
                    placeholder="이름을 입력하세요."
                    variant="standard"
                  />
                  <StyledTextField
                    id="last-name"
                    label="성 (Last Name)"
                    placeholder="성을 입력하세요."
                    variant="standard"
                  />
                </div>
                <StyledTextField
                  id="organization"
                  label="소속 (Organization)"
                  placeholder="소속을 입력하세요."
                  variant="standard"
                />
              </TextFlexRow>

              <TextFlexRow>
                <StyledTextField
                  id="email"
                  label="이메일 (E-Mail)"
                  placeholder="example@example.com"
                  variant="standard"
                />

                <StyledTextField
                  id="tel"
                  label="연락처 (Phone Number)"
                  placeholder="하이픈(-)은 빼고 입력하세요."
                  variant="standard"
                />
              </TextFlexRow>

              <StyledTextField
                id="title"
                label="제목 (Inquiry Title)"
                placeholder="문의내용이 잘 나타나도록 간단하게 요약해 주세요."
                variant="standard"
              />
            </InputContainer>

            <TextField
              id="content"
              label="문의내용 (Inquiry Content)"
              multiline
              rows={8}
              placeholder="문의내용을 자세하게 입력해 주세요.."
            />

            <UploadButton onChange={handleFileChange} useRef={fileRef} />
            <UploadFileList isMoblie={isMoblie} />
            <AgreementGuide>
              <div>개인정보 수집 및 이용에 대한 안내</div>
              <ul className="list">
                <li>
                  <span className="label">수집항목: </span>
                  <span className="value">
                    이름, 소속, 전화번호, 이메일, 문의내용
                  </span>
                </li>
                <li>
                  <span className="label">수집목적: </span>
                  <span className="value">
                    문의내용 내 사실확인에 따른 연락, 문의내용 회신
                  </span>
                </li>
                <li>
                  <span className="label">보유기간: </span>
                  <span className="value">1년 (요청 시 즉시파기)</span>
                </li>
              </ul>

              <FormControlLabel
                control={
                  <Checkbox
                    checked={checked}
                    onChange={handleCheck}
                    sx={{ svg: { fontSize: 'max(1.5rem, 24px)' } }}
                  />
                }
                sx={{
                  padding: 'max(1rem, 18px) 0',
                  '.MuiTypography-root': {
                    fontFamily: 'score',
                    color: 'black !important',
                    fontSize: 'max(1.1rem, 16px)',
                  },
                }}
                label="'개인정보 수집 및 이용에 대한 안내'를 읽었으며, 상기 내용에 동의합니다."
              />
            </AgreementGuide>

            <SubmitButton active={checked}>문 의 하 기</SubmitButton>
          </StyledBox>
        </ContentContainer>
        <FooterBar style={enableScroll ? null : { display: 'none' }} />
      </Container>
    </ScrollBody>
  )
}

const Container = styled.div`
  display: flex;
  flex-direction: column;
  height: auto;
  min-height: 100vh;
`

const ContentContainer = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: max(3rem, 40px);
  padding: max(4rem, 40px) 18rem;
  font-family: score;

  @media (max-width: 1800px) {
    padding: max(4rem, 40px) 6rem;
  }
`

const Title = styled.div`
  font-size: max(1.8rem, 24px);
  font-weight: 500;
  color: var(--primary);
`

const TextFlexRow = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 8rem;

  > div {
    position: relative;
    width: auto !important;
  }

  @media (max-width: 650px) {
    grid-template-columns: 1fr;
    gap: max(5rem, 40px);
  }
`

const StyledTextField = styled(TextField)`
  width: 100%;
`

const StyledBox = styled(Box)`
  display: flex;
  flex-direction: column;
  gap: max(2rem, 40px);

  .MuiInputBase-root {
    margin-top: 24px;
    font-family: score !important;
    font-size: max(1.2rem, 18px);
  }

  .MuiInputBase-multiline {
    margin-top: 4px !important;
  }

  .MuiFormLabel-root {
    font-family: score !important;
    font-size: max(1.2rem, 18px);
  }

  > .MuiFormControl-root {
    margin-top: max(2.1rem, 24px);
  }
`

const InputContainer = styled.div`
  display: flex;
  flex-direction: column;
  gap: max(3rem, 40px);
`

const AgreementGuide = styled.div`
  display: flex;
  flex-direction: column;

  font-family: score;
  font-weight: 500;
  font-size: max(1.3rem, 20px);

  color: var(--primary);
  margin-top: max(4rem, 36px);

  .list {
    font-size: max(1.1rem, 16px);
    line-height: max(2rem, 28px);
    padding: max(1.3rem, 21px) 0;

    list-style-type: disc;
    list-style-position: inside;
    border-bottom: 3px solid var(--light-600);
  }

  .list > li {
    display: flex;
    gap: max(0.5rem, 9px);
  }

  .list .label {
    white-space: nowrap;
  }

  .list .value {
    font-weight: 400;
    color: black;
  }
`

const SubmitButton = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: ${props => (props.active ? 'pointer' : 'not-allowed')};

  width: max(15rem, 180px);
  height: max(4rem, 60px);

  background: ${props =>
    props.active ? 'var(--primary)' : 'var(--light-700)'};
  color: white;

  font-family: score;
  font-size: max(1.3rem, 21px);

  margin: 0 auto;
`

export default Inquiry
