import styled from 'styled-components'
import Slider from 'react-slick'
import CustomArrow from '../../../../CustomArrow'
import { IconButton } from '@mui/material'
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos'
import PropTypes from 'prop-types'
import ResponsiveText from '../../../../ResponsiveText'

function SlideImage({ image }) {
  return (
    <div
      style={{
        position: 'absolute',
        backgroundImage: `url(${image})`,
        backgroundSize: 'cover',
        backgroundPosition: 'center center',
        width: '100%',
        height: '100%',
        zIndex: '-1',
      }}
    ></div>
  )
}

function Slide({ image, title, children }) {
  return (
    <SlideContainer>
      <BusinessOverlay />
      <SlideImage image={image} />
      <BusinessInfo className="not-full-height">
        <div className="title">{title}</div>
        {children}
      </BusinessInfo>
    </SlideContainer>
  )
}

Slide.propTypes = {
  image: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
}

const SlideContainer = styled.div`
  position: relative;
  object-fit: cover;
`

const BusinessOverlay = styled.div`
  position: absolute;

  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  z-index: 1;
`

const BusinessInfo = styled.div`
  position: absolute;
  height: auto;

  box-sizing: border-box;

  bottom: 0;
  padding: 2rem;

  z-index: 3;
  color: white;
  font-family: score;
  font-weight: 100;
  font-size: max(1rem, 14px);
  line-height: max(1.2rem, 18px);
  .title {
    font-weight: 500;
    font-size: max(1.6rem, 20px);
    line-height: max(2rem, 32px);
  }
`

function BusinessSlider() {
  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToScroll: 1,
    variableWidth: true,
    nextArrow: (
      <CustomArrow
        name="next"
        icon={
          <IconButton>
            <ArrowForwardIosIcon />
          </IconButton>
        }
      />
    ),
  }
  return (
    <StyledSlider {...settings}>
      <Slide
        image={process.env.PUBLIC_URL + '/images/business_plan.jpg'}
        title="건축설계"
      >
        <ResponsiveText>건축설계에 대한 설명입니다.</ResponsiveText>
        <ResponsiveText>여기에 쓰고싶은거 쓰세요.</ResponsiveText>
        <ResponsiveText>3줄정도 쓰시면 될거같네요.</ResponsiveText>
      </Slide>
      <Slide
        image={process.env.PUBLIC_URL + '/images/business_structure.jpg'}
        title="구조설계"
      >
        <ResponsiveText>건축설계에 대한 설명입니다.</ResponsiveText>
        <ResponsiveText>여기에 쓰고싶은거 쓰세요.</ResponsiveText>
        <ResponsiveText>3줄정도 쓰시면 될거같네요.</ResponsiveText>
      </Slide>
      <Slide
        image={process.env.PUBLIC_URL + '/images/business_contract.jpg'}
        title="건축사업 관리"
      >
        <ResponsiveText>건축설계에 대한 설명입니다.</ResponsiveText>
        <ResponsiveText>여기에 쓰고싶은거 쓰세요.</ResponsiveText>
        <ResponsiveText>3줄정도 쓰시면 될거같네요.</ResponsiveText>
      </Slide>
      <Slide
        image={process.env.PUBLIC_URL + '/images/business_interior.jpg'}
        title="인테리어 설계"
      >
        <ResponsiveText>건축설계에 대한 설명입니다.</ResponsiveText>
        <ResponsiveText>여기에 쓰고싶은거 쓰세요.</ResponsiveText>
        <ResponsiveText>3줄정도 쓰시면 될거같네요.</ResponsiveText>
      </Slide>
    </StyledSlider>
  )
}

const StyledSlider = styled(Slider)`
  height: 100%;

  div:not(.not-full-height) {
    height: 100%;
  }

  .slick-slide {
    width: 3%;
    margin-right: 20px;
  }

  .slick-prev {
    display: none !important;
  }

  .next {
    width: max(4rem, 64px) !important;
    height: max(4rem, 64px) !important;
    border-radius: 9999%;
    background: rgba(0, 0, 0, 0.6);
    position: absolute !important;
    top: 50%;
    right: 0;
    transform: translate(-100%, -50%);
  }

  .next button {
    width: 100%;
    height: 100%;
  }

  .next svg {
    color: white;
    font-size: max(1.5rem, 24px);
  }

  @media (max-width: 650px) {
    .slick-slide {
      width: 5%;
    }
  }
`

export default BusinessSlider
